<template>
	<div class="home">
		<div class="content-left">
			<sidebar ref='side' @addTabs="addTab(editableTabsValue)"></sidebar>
		</div>
		<div class="content-right">
			<div class="content-head">
				<navbar></navbar>
			</div>
			<div class="content-nav">
				<el-tabs v-model="editableTabsValue" type="card" closable @tab-remove="removeTab" @tab-click="routers($event)">
					<el-tab-pane v-for="(item) in editableTabs" :key="item.name" :label="item.title" :name="item.name" >	
					</el-tab-pane>
				</el-tabs>
			</div>
			<div class="container">
				<keep-alive>
					<router-view></router-view>
				</keep-alive>
			</div>
		</div>
	</div>
</template>

<script>
	import sidebar from './common/sidebar.vue'
	import navbar from './common/navbar.vue'
	export default {
		components: {
			sidebar,
			navbar
		},
		data() {
			return {
				editableTabsValue: this.$route.name,
				editableTabs: [{
					title: this.$route.meta.title,
					name: this.$route.name,
				}],
			}
		},
		mounted() {
			
		},
		methods: {
			routers($event){
				if (this.$route.name != $event.name) {
					this.$router.push({name:$event.name})
				}
			},
			addTab(targetName) {
				let Name = this.$refs.side.name;
				this.editableTabsValue = Name;
				let edit = this.editableTabs;
				for (var i = 0;i<edit.length;i++) {
					if (Name == edit[i].name) {
						return false;
					}
				}
				this.editableTabs.push({
					title: this.$refs.side.title,
					name: Name
				});
				this.editableTabsValue = Name;
			},
			removeTab(targetName) {
				let index = this.editableTabs.length;
				if (index == 1 && targetName == 'dashboard') {
					this.$message({
					    message: '这里已经是主界面了',
					    type: 'warning'
					});
					return;
				}
				let tabs = this.editableTabs;
				let activeName = this.editableTabsValue;
				if (activeName === targetName) {
					tabs.forEach((tab, index) => {
						if (tab.name === targetName) {
							let nextTab = tabs[index + 1] || tabs[index - 1];
							if (nextTab) {
								activeName = nextTab.name;
							}
						}
					});
				}
				
				this.editableTabsValue = activeName;
				this.editableTabs = tabs.filter(tab => tab.name !== targetName);
				if (index == 1 && this.$route.name != 'dashboard') {
					this.$router.push({name:'dashboard'});
					this.editableTabs.push({
						title: "首页",
						name: 'dashboard'
					});
					this.editableTabsValue = 'dashboard';
				}else if(index > 1 && this.$route.name == targetName){
					this.$router.push({name:activeName});
				}
			}
		}
	}
</script>
<style lang="scss" scoped>
	.home {
		width: 100%;
		min-height: 100%;
		display: flex;
		
		.content-left {
			width: 300px;
			min-height: 100%;
		}
		.content-right {
			width: calc(100% - 300px);
			.content-head {
				height: 60px;
				width: 100%;
				background: rgb(240, 240, 240);
			}
			.content-nav{
				width: 100%;
			}
			.container {
				width: 100%;
				box-sizing: border-box;
				padding: 0 15px;
			}
		}
	}
</style>
