<template>
	<div class="navbar">
		<div class="navbar-user">
			<div class="user-head"><small>{{userName}}</small></div>
			<div class="logout" @click="logout()">退出登录</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				userName:sessionStorage.getItem('userName')
			}
		},
		methods: {
			logout() {
				this.$confirm('确定要退出登录吗?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$message({
						type: 'success',
						message: '退出成功!'
					});
					sessionStorage.removeItem('userName')
					this.$router.push('/login')
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消退出'
					});
				});
			},
		}
	}
</script>

<style lang="scss" scoped>
	.navbar {
		width: 100%;
		height: 100%;
		.navbar-user{
			width: 60px;
			height: 60px;
			position: relative;
			float: right;
			margin-right: 10px;
			&:hover .logout{
				display: block;
			}
			.user-head{
				width: 40px;
				height: 40px;
				position: absolute;
				left: 50%;
				top: 50%;
				border-radius: 50%;
				background-color: #409EFF;
				color: #ffffff;
				line-height: 40px;
				text-align: center;
				font-size: 12px;
				transform: translate(-50%,-50%);
			}
			.logout{
				width: 100%;
				padding: 5px 0px;
				font-size: 12px;
				display: none;
				position: absolute;
				left: 0;
				top: 50px;
				text-align: center;
				color: #ffffff;
				border-radius: 3px;
				background: rgba(0,0,0,0.8);
			}
		}
	}
</style>
