import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store/index.js'
import login from '../views/login.vue'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [{
		path: '/',
		redirect: '/login'
	},
	{
		path: '/login',
		name: 'login',
		component: login,
		meta:{
			title:'图片版权管理系统'
		}
	},
	{
		path: '/home',
		name: 'home',
		component: Home,
		children:[
			{
				path: '/dashboard',
				name: 'dashboard',
				component: () => import('../views/dashboard/index.vue'),
				meta:{
					title:'首页'
				}
			},
			{
				path: '/picture',
				name: 'picture',
				component: () => import('../views/manage/picture.vue'),
				meta:{
					title:'图片管理'
				}
			},
			{
				path: '/uploading',
				name: 'uploading',
				component: () => import('../views/manage/uploading.vue'),
				meta:{
					title:'上传结果'
				}
			},
			{
				path: '/consumer',
				name: 'consumer',
				component: () => import('../views/system/consumer.vue'),
				meta:{
					title:'用户中心'
				}
			},
			{
				path: '/enrolment',
				name: 'enrolment',
				component: () => import('../views/system/enrolment.vue'),
				meta:{
					title:'注册'
				}
			},
			{
				path: '/character',
				name: 'character',
				component: () => import('../views/system/character.vue'),
				meta:{
					title:'角色管理'
				}
			},
			{
				path: '/menu',
				name: 'menu',
				component: () => import('../views/system/menu.vue'),
				meta:{
					title:'菜单管理'
				}
			}
		]
	},
	{
		path: '/about',
		name: 'About',
		component: () => import('../views/About.vue')
	}
]

const router = new VueRouter({
	routes
})
router.beforeEach((to, from, next) => {
	// let userName = store.state.userName
	let userName = sessionStorage.getItem('userName');
	if (to.name !== 'login' && userName == ''){
		next({ name: 'login' })
		// console.log(userName);
	}else{
		next()
		// console.log(userName);
	}
})
export default router
