<template>
	<div class="sidebar">
		<div class="sidebar-head"><i class="el-icon-picture-outline-round"></i>图片版权管理系统</div>
		<el-menu :default-active="$route.name" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
			background-color="#2E3E55" text-color="#fff" active-text-color="#4C8DD6 !important" router>
			<template v-for="item in items">
			    <template v-if="item.subs">
			        <el-submenu :index="item.index" :key="item.index">
			            <template slot="title">
			                <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
			            </template>
			            <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index" @click="getName(subItem.index,subItem.title)">
			                <i :class="subItem.icon"></i>
			                <span slot="title">{{ subItem.title }}</span>
			            </el-menu-item>
			        </el-submenu>
			    </template>
			    <template v-else>
			        <el-menu-item :index="item.index" :key="item.index" @click="getName(item.index,item.title)">
			            <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
			        </el-menu-item>
			    </template>
			</template>
		</el-menu>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				items: [{
				        icon: 'iconfont el-icon-house',
				        index: 'dashboard',
				        title: '首页',
				    },
				    {
				        icon: 'iconfont el-icon-coin',
				        index: 'manage',
				        title: '图片版权管理',
				        subs: [{
				            index: 'picture',
				            title: '图片管理',
				            icon: 'iconfont el-icon-picture-outline',
				        },{
				            index: 'uploading',
				            title: '上传结果',
				            icon: 'iconfont el-icon-picture-outline',
				        }]
				    },
				    {
				        icon: 'iconfont el-icon-setting',
				        index: 'system',
				        title: '系统管理',
				        subs: [{
				            index: 'consumer',
				            title: '用户管理',
				            icon: 'iconfont el-icon-document',
				        },{
				            index: 'enrolment',
				            title: '注册',
				            icon: 'iconfont el-icon-document',
				        },{
				            index: 'character',
				            title: '角色管理',
				            icon: 'iconfont el-icon-document',
				        },{
				            index: 'menu',
				            title: '菜单管理',
				            icon: 'iconfont el-icon-document',
				        }]
				    },
				],
				name:'',
				title:'',
			}
		},
		methods: {
			getName(name,title){
				if (name != this.name) {
					this.name=name;
					this.title=title;
					this.$emit("addTabs");
				}
			},
			handleOpen(key, keyPath) {
				// console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				// console.log(key, keyPath);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.sidebar {
		height: 100%;
		background-color: #2E3E55;
		.sidebar-head{
			height: 60px;
			width: 100%;
			background: #1E2E3E;
			text-align: center;
			font-size: 16px;
			color: #ffffff;
			letter-spacing: 1px;
			line-height: 60px;
			>i{
				margin-right: 10px;
				font-size: 20px;
				position: relative;
				top: 2px;
				color: #41BA84;
			}
		}
		.el-menu{
			border-right: 0;
			box-sizing: border-box;
			.el-menu-item{
				i{
					font-size: 14px;
					padding-right: 10px;
					color: rgb(255, 255, 255);
				}
			}
			.el-submenu__title{
				i{
					font-size: 14px;
					padding-right: 10px;
					color: rgb(255, 255, 255);
				}
			}
			.is-active{
				>i{
					color: #4C8DD6;
				}
			}
		}
	}
</style>
